import Vue from 'vue'
import Router from 'vue-router'

const Main = resolve => require(['@/components/Main'],resolve)
const Home = resolve => require(['@/components/common/secondRouter/Home'],resolve)
const Component404 = resolve => require(['@/components/common/Component404'],resolve)
const FinancialCenter = resolve => require(['@/components/common/secondRouter/FinancialCenter'],resolve)
const OperationCenter = resolve => require(['@/components/common/secondRouter/OperationCenter'],resolve)
const ProductCenter = resolve => require(['@/components/common/secondRouter/ProductCenter'],resolve)
const RoutingCenter = resolve => require(['@/components/common/secondRouter/RoutingCenter'],resolve)
const TradingCenter = resolve => require(['@/components/common/secondRouter/TradingCenter'],resolve)


const HomeSystem = resolve => require(['@/components/Main/Home/System'],resolve)

Vue.use(Router)


const routes = [
  {
    path: '/',
    name: '首页',
    component: Main,
    redirect: '/home',
    children:[
      {
        path:'home',
        component:Home,
        name:'系统首页1',
        redirect: '/home/system',
        children:[
          {
            path:'system',
            redirect:'/home/system/1'
          },
          {
            path:'system/:page',
            name:'系统首页',
            component: HomeSystem
          }
        ]
      },
      {
        path:'financialCenter',
        name:'财务中心',
        component:FinancialCenter
      },
      {
        path:'operationCenter',
        name:'运营中心',
        component:OperationCenter
      },
      {
        path:'productCenter',
        name:'产品中心',
        component:ProductCenter
      },
      {
        path:'routingcenter',
        name:'路由中心',
        component:RoutingCenter
      },
      {
        path:'tradingCenter',
        name:'交易中心',
        component:TradingCenter
      },
      // {
      //   path:'*',
      //   component:Component404
      // }
    ],
  },
  {
    path:'*',
    component: Component404
  }
]

export default new Router({
  mode: 'history',
  routes,
  scrollBehavior (to,from,savedPosition){
    return {x:0,y:0}
  }
})



