<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App'
  }
</script>

<style>
  /* 初始化 */
  body,ul,ol,p,h1,h2,h3,h4,h5,dl,dd,form,input,textarea,td,th,button,strong,em,select,video,canvas{margin:0;padding:0;}
  li{list-style:none;vertical-align:top;}
  table{ border-collapse:collapse;}
  textarea{resize:none;overflow:auto;}
  img{ border:none; vertical-align:middle;}
  em{ font-style:normal;}
  a{ text-decoration:none; color: #48576a;}
  a,input{ -webkit-tap-highlight-color:rgba(0,0,0,0);}
  input{outline:0px;}
  body{background:#f1f1f1; margin: 0; padding: 0; font-family:'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;}
  /* 公共样式 */
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .el-form > div {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
  .search {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
  .search > div {
    height: 50px;
    line-height: 50px;
  }
  .el-table tr {
    line-height: 40px;
  }
  .el-table tr > th,.el-table tr > th {
    text-align: center;
  }
  .el-pagination {
    white-space: nowrap;
    padding: 2px 5px;
    color: #303133;
    font-weight: bold;
    line-height: 0px;
    text-align: right;
    padding: 10px;
  }
  .el-dialog__header {
    line-height: 0px;
  }
</style>



