import * as types from './mutation-types'

const actions = {
    addUserInfo ({commit,state}, newUserInfo) {
      commit(types.ADD_USER_INFO,newUserInfo)
    },
    delUserInfo ({commit,state}) {
      commit(types.DEL_USER_INFO)
    }
  }

export default actions



